import { Component, Input, OnInit } from '@angular/core';
import { LivraisonService } from '../livraison.service';

@Component({
  selector: 'app-panier',
  templateUrl: './panier.component.html',
  styleUrls: ['./panier.component.css']
})
export class PanierComponent implements OnInit {




supprimerProduitAuPanier(quelProduit:object){

  let indexDuProduitASupprimer = this.lePanier.indexOf(quelProduit);

  this.lePanier.splice(indexDuProduitASupprimer,1);
}


calculDuTotal(){

let monTotal = 0;

this.lePanier.forEach((chaqueProduit:any)=>{

  monTotal+= chaqueProduit.prix;
  
})
     return monTotal;
  
}

@Input () lePanier:any;

constructor(private unService : LivraisonService) { }

  ngOnInit(): void {

    this.lePanier = this.unService.livrerProduit();
    
   
  }

}



